.background{
    height: 100%;
    width: 100%;
    float: left;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: none;
    position: fixed;
    object-fit: cover;
    z-index: -1;
}

@keyframes FadeIn{
   from{opacity: 0;} 
   to {opacity: 1;}
}

.fade-in{
    animation: FadeIn 500ms ease-in-out;
}